class EventBus {
    [x: string]: (type: string, ...args: any[]) => void;
    public events?: any;
    constructor() {
        this.events = this.events || {};
    }
}
//event
//type
EventBus.prototype.emit = function (type: string, ...args:Array<any>) {
    let e;
    e = this.events[type];
    // typeevent
    if (Array.isArray(e)) {
        for (let i = 0; i < e.length; i++) {
            e[i].apply(this, args);
        }
    } else {
        e[0].apply(this, args);
    }
};
//type
EventBus.prototype.addListener = function (type: string, fun: Function) {
    const e = this.events[type];

    if (!e) {   //
        this.events[type] = [fun];
    } else {  //
        e.push(fun);
    }
};
const eventBus = new EventBus();
export default eventBus;